// 1. å¼‚æ­¥æ›´æ–°State,çŸ­æ—¶é—´å†…æŠŠå¤šä¸ªStateåˆå¹¶ä¸ºä¸€ä¸ª(é˜Ÿåˆ—) å…ˆè¿›å…ˆå‡º
import { renderComponent } from "../react-dom/diffPart";
// 2.ä¸€æ®µæ—¶é—´ä¹‹åï¼Œå¾ªç¯æ¸…ç©ºé˜Ÿåˆ—,æ¸²æŸ“ç»„ä»¶

// æ•°ç»„æ¨¡æ‹Ÿé˜Ÿåˆ—
const setStateQueue = [];
// ä¿å­˜å½“å‰ç»„ä»¶
const renderQueue = [];

const defer = (fn) => {
  return Promise.resolve().then(fn);
};

export const enqueueSetState = (stateChange, component) => {
  if (setStateQueue.length === 0) {
    // setTimeout(() => {
    //   flush();
    // }, 0);
    defer(flush);
  }
  // 1.çŸ­æ—¶é—´å†…åˆå¹¶å¤šä¸ªSetState;
  setStateQueue.push({
    stateChange,
    component,
  });

  //  å¦‚æœrenderQueueé‡Œé¢æ²¡æœ‰ç»„ä»¶, æ·»åŠ åˆ°é˜Ÿåˆ—ä¸­
  let r = renderQueue.some((item) => {
    return item === component;
  });
  if (!r) {
    // è¯æ˜ç¬¬ä¸€æ¬¡æ·»åŠ 
    renderQueue.push(component);
  }
};

// ä¸€æ®µæ—¶é—´ä¹‹å
const flush = () => {
  let item, component;
  // å¯¹åˆ—ä¸­å·²æœ‰å€¼
  // while ((item = setStateQueue.pop())) {
  while ((item = setStateQueue.shift())) {
    // console.log(item, "dsd");
    const { stateChange, component } = item;
    // ä¿å­˜ä¹‹å‰çš„çŠ¶æ€
    if (!component.prevState) {
      component.prevState = Object.assign({}, component.state);
    }

    if (typeof stateChange === "function") {
      //  å¦‚æœä¸ºå‡½æ•°
      Object.assign(
        component.state,
        stateChange(component.prevState, component.props)
      );
    } else {
      Object.assign(component.state, stateChange);
    }
    // èµ‹å€¼
    component.prevState = component.state;
  }

  while ((component = renderQueue.shift())) {
    renderComponent(component);
  }
};
